body {
  background: #f9f9f9;
  font-size: 16px;
  font-family: "Raleway", sans-serif;
}

.container {
  padding: 1em 0;
  float: left;
  height: 300px;
  width: 95%;
  border: 0px solid rgb(255, 221, 221);
  border-radius: 10px;
  box-shadow: 0px 0px 5px 1px rgb(205, 205, 205);
  background-color: black;
  margin-bottom: 50px;
}

.container .title {
  color: #1a1a1a;
  text-align: center;
  margin-bottom: 10px;
}

.content {
  position: relative;
  width: 90%;
  max-width: 600px;
  margin: auto;
  overflow: hidden;
}

.container1 {
  padding: 1em 0;
  float: left;
  height: 300px;
  width: 95%;
  border: 0px solid rgb(250, 250, 250);
  border-radius: 10px;
  /* box-shadow: 0px 0px 5px 1px rgb(205, 205, 205); */
  background-color: #f9f9f9;
  margin-bottom: 50px;
  cursor: default;
}

.content1 {
  position: relative;
  width: 90%;
  max-width: 600px;
  height: 250px;
  margin: auto;
  overflow: hidden;
}

.content .content-overlay {
  background: rgba(0, 0, 0, 0.9);
  position: absolute;
  height: 99%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s;
}

.content:hover .content-overlay {
  opacity: 1;
}

.content-image {
  width: 100%;
}

.content-details {
  position: absolute;
  text-align: center;
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.content:hover .content-details {
  top: 50%;
  left: 50%;
  opacity: 1;
}

.content-details h3 {
  color: #3498db;
  font-weight: 600;
  letter-spacing: 0.15em;
  margin-bottom: 0.5em;
  text-transform: uppercase;
}

.content-details p {
  color: #3498db;
  font-size: 0.8em;
}

.fadeIn-top {
  top: 20%;
}

.fadeIn-left {
  left: 20%;
}

.gitlive {
  margin: auto;
  margin-top: 8px;
  justify-content: center;
  display: flex;
  flex-direction: row;
}

a {
  text-decoration: none;
}
